.hero-container {
    position: relative;
    text-align: center;
    color: black;
    top: 0;
    left: 0;
}

.hero-img {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    filter: brightness(36%);
}

.hero-title {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 3.5rem;
    font-weight: 700;
    min-width: 800px;
}

.hero-title-short {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 1.3rem;
    font-weight: 500;
    margin-top: 50px;
    min-width: 400px;
}

@media screen and (max-width:1240px) {
    .hero-img {
        width: 100%;
    }
    .hero-title {
        font-size: 2.4rem;
        transform: translate(-50%, -90%);
        min-width: 500px;
    }
    .hero-title-short {
        font-size: 0.9rem;
    }
}

@media screen and (max-width:740px) {
    .hero-img {
        width: 100%;
        top: 45%;
    }
    .hero-title {
        font-size: 2rem;
        transform: translate(-50%, -60%);
    }
    .hero-title-short {
        font-size: 0.9rem;
    }
}

@media screen and (max-width:500px) {
    .hero-img {
        width: 100%;
        top: 45%;
    }
    .hero-title {
        transform: translate(-50%, -60%);
        min-width: 300px;
        font-size: 1.6rem;
    }
    .hero-title-short {
        font-size: 0.7rem;
        min-width: 200px;
    }
}
